import myScrollX from '@/components/my-scrollX/my-scrollX.vue'
export default {
	components: {
		myScrollX
	},
	data() {
		return {
			articleName:'',
			scrollTopList:[{
				organizeName: '全部',
				id: ''
			}],
			currentTopTab: 0,
			beforeColor: '#666',
			afterColor: '#2E3B67',
			articleList: [],
			organizeId: '',// 分组id
			page:1,
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '- 暂时没有新内容了呢 -'
			},
			status: 'loading',
			code:'',
		}
	},
	onPullDownRefresh() {
		this.page = 1;
		this.articleList = []
		this.scrollTopList = [{
			organizeName: '全部',
			id: ''
		}]
		this.articleName = ''
		uni.showLoading({
			title: '加载中'
		});
		this.getTrainList()
		uni.hideLoading();
		uni.stopPullDownRefresh()
	},
	onReachBottom(){
		this.scrollTopList = [{
			organizeName: '全部',
			id: ''
		}]
		if (this.code != '-116') {
			this.page = this.page + 1;
			this.getTrainList();
		}
	},
	onShow(){
		this.page = 1;
		this.articleList = []
		this.scrollTopList = [{
			organizeName: '全部',
			id: ''
		}]
		this.currentTopTab = 0
		this.organizeId = ''
		this.articleName = ''
		this.getTrainList()
	},
	methods:{
		// 获取数据列表
		getTrainList(){
			if(uni.getStorageSync('userInfo')){
				let memberId = JSON.parse(uni.getStorageSync('userInfo')).id
				this.$Request.get(`/appArticleController.do?getMyArticleList&memberId=${memberId}&page=${this.page}&articleName=${this.articleName}&organizeId=${this.organizeId}`)
				.then(res => {
					this.code = res.code
					let scrollTopList = []
					this.scrollTopList = this.scrollTopList.concat(res.data.organizeList).map(item => {
						return {
							...item,
							groupName: item.organizeName
						}
					})
					this.status = 'noMore'
					if(res.code == 0){
						this.articleList =  [...this.articleList, ...res.data.articleList].map(item => {
							return {
								...item,
								createDate: item.createDate && item.createDate.substring(0,10)
							}
						})
					}else if(res.code == '-118' || res.code == '-116'){
						this.status = 'noMore'
					}else{
						uni.showToast({
							title: res.info,
							icon: 'none'
						})
					}
				})
			}else{
				uni.showToast({
					title: '您尚未登录，正在跳往登录页面。。。',
					icon: 'none'
				})
				setTimeout(() => {
					uni.navigateTo({
						url:'/pages/loginAll/login'
					})
				}, 1000)
			}
		},
		// 收藏
		clickAttention(item, index){
			let memberId = JSON.parse(uni.getStorageSync('userInfo')).id
			let articleId = item.id
			this.$Request.get(`/appAttentionController.do?takeArticleAttention&memberId=${memberId}&articleId=${articleId}&organizeId=${this.organizeId}`)
			.then(res => {
				if(res.code == 0){
					this.articleList[index].attentionNum = item.attentionType == 1 ? item.attentionNum - 1 : item.attentionNum + 1
					this.articleList[index].attentionType = item.attentionType == 1 ? 0 : 1
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		},
		bindNameInput(e){
			this.scrollTopList = [{
				organizeName: '全部',
				id: ''
			}]
			this.articleName = e.target.value
			this.articleList = []
			this.page = 1;
			this.getTrainList()
		},
		clickSearch(){
			this.scrollTopList = [{
				organizeName: '全部',
				id: ''
			}]
			this.articleList = []
			this.page = 1;
			this.getTrainList()
		},
		tabChange(item, index){
			this.currentTopTab = index
			this.page = 1;
			this.articleList = []
			this.organizeId = item.id
			this.scrollTopList = [{
				organizeName: '全部',
				id: ''
			}]
			this.getTrainList()
			// console.log(item, index)
		},
		gotoUrl(){
			uni.navigateTo({
				url: '/pages/discover/discoverDetail'
			})
		},
		gotoGroup(){
			uni.navigateTo({
				url: '/pages/discover/groupManage'
			})
		}
	}
}
